import React from 'react';
import styled from 'styled-components';
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";

const PopularThemeBlock = styled.div`
  margin-top:130px;
  button{
    padding:1.5rem 1rem;
    font-size:1rem;
    color:white;
    background:black;
    border-radius:10px;
    white-space: nowrap;
    font-weight:bold;
    &:first-child{
      margin-left:40px;
    }
  }
  padding-bottom:40px;
  border-bottom:8px solid #EEEEEE;
  
`

function PopularTheme(){
  const themeList = [1,2,3,4] //  props  
                              //   
  return(
    <PopularThemeBlock>
      <Swiper
        slidesPerView={3}
        spaceBetween={30}
      > 
      {
        themeList.map((a,i) => {
          return <SwiperSlide key={i}> <button># </button> </SwiperSlide>
        })
      }
      </Swiper>

    </PopularThemeBlock>
  )
}

export default PopularTheme;